import dynamic from 'next/dynamic';

const StoreInfoPage = dynamic(
  () => import('@/features/settings/store-info/store-info-page').then(mod => mod.StoreInfoPage),
  { ssr: false, loading: () => <div className="flex items-center justify-center h-64"><div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div></div> }
);

export default function Page() {
  return <StoreInfoPage />;
}
